import { useState, useEffect, useMemo } from 'react'
import { View, Text } from '@tarojs/components'
import Taro, { useRouter } from '@tarojs/taro'
import TherapistHeader from '../../../components/TherapistHeader'
import SymptomCategoryTabs from '../../../components/SymptomCategoryTabs'
import SymptomServiceList from '../../../components/SymptomServiceList'
import ShoppingCart from '../therapist/components/ShoppingCart'
import { symptomService } from '../../../services/symptom'
import { therapistService } from '../../../services/therapist'
import './index.scss'

interface CartItem {
  id?: string  // ÂîØ‰∏ÄÊ†áËØÜÁ¨¶ÔºàÂèØÈÄâÔºåÂêëÂêéÂÖºÂÆπÔºâ
  serviceId: string
  serviceName: string
  duration: number
  price: number
  discountPrice?: number
  date: string
  time: string
  therapistId: string
  therapistName: string
  therapistAvatar?: string
}

const SymptomPage = () => {
  const router = useRouter()
  const { storeId, storeName, selectedDate, selectedTime } = router.params

  // Ëß£Á†ÅURLÂèÇÊï∞‰∏≠ÁöÑÊó∂Èó¥ÔºàÂ§ÑÁêÜURLÁºñÁ†ÅÁöÑÂÜíÂè∑Ôºâ
  const decodedTime = selectedTime ? decodeURIComponent(selectedTime as string) : ''

  const [therapists, setTherapists] = useState<any[]>([])
  const [categories, setCategories] = useState<any[]>([])
  const [services, setServices] = useState<any[]>([])
  const [activeCategoryId, setActiveCategoryId] = useState('')
  const [cartItems, setCartItems] = useState<CartItem[]>([])
  const [loading, setLoading] = useState(true)

  // Ëé∑ÂèñËØ•Èó®Â∫óÁöÑÊâÄÊúâÊé®ÊãøÂ∏à + ÊéíÁè≠‰ø°ÊÅØ
  useEffect(() => {
    if (storeId && selectedDate) {
      const fetchTherapists = async () => {
        try {
          // Âπ∂Ë°åËé∑Âèñ‰∏§‰∏™Êï∞ÊçÆÊ∫ê
          const [basicInfo, availabilityData] = await Promise.all([
            therapistService.getTherapistsByStore(storeId as string),
            therapistService.getTherapistsAvailability(
              storeId as string,
              selectedDate as string
            )
          ])

          // ÂêàÂπ∂Âü∫Êú¨‰ø°ÊÅØÂíåÊéíÁè≠Êï∞ÊçÆ
          const merged = basicInfo.list.map(therapist => {
            const availability = availabilityData.find((a: any) => a.id === therapist.id)
            return {
              ...therapist,
              availability: availability?.availability || []
            }
          })

          console.log('‚úÖ ÊäÄÂ∏àÊï∞ÊçÆÂêàÂπ∂ÊàêÂäü:', merged)
          setTherapists(merged)
        } catch (error) {
          console.error('‚ùå Ëé∑ÂèñÊäÄÂ∏à‰ø°ÊÅØÂ§±Ë¥•:', error)
          // Fallback: Âè™‰ΩøÁî®Âü∫Êú¨‰ø°ÊÅØ
          therapistService.getTherapistsByStore(storeId as string).then(res => {
            setTherapists(res.list)
          })
        }
      }

      fetchTherapists()
    }
  }, [storeId, selectedDate])

  // Ëé∑ÂèñÁóáÁä∂ÂàÜÁ±ª
  useEffect(() => {
    symptomService.getCategories().then(res => {
      setCategories(res.data)
      if (res.data.length > 0) {
        setActiveCategoryId(res.data[0].id)
      }
    })
  }, [])

  // Ëé∑ÂèñËØ•Èó®Â∫óÊâÄÊúâÊé®ÊãøÂ∏àÁöÑÊúçÂä°ÂàóË°®
  useEffect(() => {
    if (storeId) {
      setLoading(true)
      symptomService.getStoreSymptomServices(storeId as string).then(res => {
        setServices(res.data)
        setLoading(false)
      })
    }
  }, [storeId])

  // Ê†πÊçÆÂàÜÁ±ªÁ≠õÈÄâÊúçÂä°
  const filteredServices = useMemo(() => {
    return services.filter(service => service.categoryId === activeCategoryId)
  }, [services, activeCategoryId])

  // Ë¥≠Áâ©ËΩ¶‰∏≠ÁöÑÊúçÂä°IDÂàóË°®
  const cartServiceIds = useMemo(() => {
    return cartItems.map(item => item.serviceId)
  }, [cartItems])

  // Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶
  const handleAddToCart = (service: any, therapistId: string) => {
    console.log('üéØ Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶ - therapistId:', therapistId)
    const therapist = therapists.find(t => t.id === therapistId)
    console.log('üéØ ÊâæÂà∞ÁöÑÊäÄÂ∏à:', therapist)

    if (!therapist) {
      console.error('‚ùå Êú™ÊâæÂà∞ÊäÄÂ∏àÔºåtherapistId:', therapistId)
      return
    }

    const newItem: CartItem = {
      serviceId: service.id,
      serviceName: service.name,
      duration: service.duration,
      price: service.price,
      discountPrice: service.discountPrice,
      date: selectedDate as string || new Date().toISOString().split('T')[0],
      time: decodedTime || '10:00',  // ‰ΩøÁî®Ëß£Á†ÅÂêéÁöÑÊó∂Èó¥ÔºåÈªòËÆ§10:00
      therapistId: therapist.id,
      therapistName: therapist.name,
      therapistAvatar: therapist.avatar
    }

    console.log('üéØ Êñ∞Ë¥≠Áâ©ËΩ¶È°πÁõÆ:', newItem)
    console.log('üéØ Êñ∞È°πÁõÆÁöÑtherapistId:', newItem.therapistId)

    setCartItems([...cartItems, newItem])

    Taro.showToast({
      title: 'Â∑≤Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶',
      icon: 'none'
    })
  }


  // ÂéªÁªìÁÆó
  // Ê∏ÖÁ©∫Ë¥≠Áâ©ËΩ¶
  const handleClearCart = () => {
    setCartItems([])
    Taro.showToast({
      title: 'Ë¥≠Áâ©ËΩ¶Â∑≤Ê∏ÖÁ©∫',
      icon: 'none'
    })
  }

  // Âà†Èô§Âçï‰∏™ÂïÜÂìÅ
  const handleRemoveItem = (index: number) => {
    const newItems = cartItems.filter((_, i) => i !== index)
    setCartItems(newItems)
    Taro.showToast({
      title: 'Â∑≤ÁßªÈô§ÂïÜÂìÅ',
      icon: 'none'
    })
  }

  const handleCheckout = () => {
    if (cartItems.length === 0) {
      Taro.showToast({
        title: 'ËØ∑ÂÖàÈÄâÊã©ÊúçÂä°È°πÁõÆ',
        icon: 'none'
      })
      return
    }

    console.log('üîÑ ÂáÜÂ§áÁªìÁÆóÔºåË¥≠Áâ©ËΩ¶ÂÜÖÂÆπ:', cartItems)
    console.log('üîÑ Á¨¨‰∏Ä‰∏™È°πÁõÆ:', cartItems[0])
    console.log('üîÑ Á¨¨‰∏Ä‰∏™È°πÁõÆÁöÑtherapistId:', cartItems[0]?.therapistId)

    const params = {
      items: JSON.stringify(cartItems),
      storeId,
      storeName,
      from: 'symptom'
    }

    console.log('üîÑ ‰º†ÈÄíÁöÑÂèÇÊï∞:', params)

    Taro.navigateTo({
      url: `/pages/booking/confirm/index?${Object.entries(params)
        .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
        .join('&')}`
    })
  }

  if (loading) {
    return <View className="symptom-page loading">Âä†ËΩΩ‰∏≠...</View>
  }

  return (
    <View className="symptom-page">

      {/* ‰∏ªÂÜÖÂÆπÂå∫ */}
      <View className="symptom-content">
        {/* Â∑¶‰æßÂàÜÁ±ªÊ†áÁ≠æ */}
        <SymptomCategoryTabs
          categories={categories}
          activeId={activeCategoryId}
          onChange={setActiveCategoryId}
        />

        {/* Âè≥‰æßÊúçÂä°ÂàóË°® - ÊåâÊé®ÊãøÂ∏àÂàÜÁªÑÂ±ïÁ§∫ */}
        <SymptomServiceList
          services={filteredServices}
          therapists={therapists}
          selectedDate={selectedDate as string}
          selectedTime={decodedTime}
          onAddToCart={handleAddToCart}
          cartServiceIds={cartServiceIds}
        />
      </View>

      {/* Â∫ïÈÉ®Ë¥≠Áâ©ËΩ¶ */}
      <ShoppingCart
        items={cartItems}
        onCheckout={handleCheckout}
        onMaskClick={handleClearCart}
        onRemoveItem={handleRemoveItem}
        simpleClearMode={true}
      />
    </View>
  )
}

export default SymptomPage